<template>
  <van-row>
    <van-col class="line" v-for="(item, index) in parentData" :key="index">
      <div class="title">
        <div class="order-type">
          <span>{{ item.poiId ? item.poiId : '商城订单'}}</span>
        </div>
        <div v-if="item.orderStatusName !== '售后处理'" :class="{'state': true, 'color-wine-red': item.orderStatusName === '已确认'}">
          <span v-if="item.orderStatusName === '已付款待分享'" > 已支付，待分享</span>
          <span v-else>{{item.orderStatusName || ''}}</span>
        </div>
      </div>
      <template v-for="(itemChild, indexChild) in item.orderProducts">
        <div class="commodity" @click="viewDetail(itemChild, item)" :key="`commodity${indexChild}`">
          <div class="reserve" v-if="itemChild.isBook && !item.zActivityOrder">预订</div>
          <img class="thumbnail" :src="(itemChild.defaultImageUrl ? itemChild.defaultImageUrl  : 'http://cdn02.ehaier.com/product/4e2fbaa934198c4c7a00001e.jpg') | imageToHttps" alt="商品封面图"/>
          <div style="width: 4.7rem;">
            <div style="display: flex; flex-direction: 'row">
              <div class="introduce">
                <div class="product-title">
                  {{itemChild.productFullName || ''}}
                </div>
                <div class="model">
                  {{itemChild.productName || ''}}
                </div>
                <div class="model" v-if="itemChild.attrName">
                  已选 {{itemChild.attrName || ''}}
                </div>
              </div>
              <div class="amount-money">
                <template v-if="!(item.ordersExtendVo && item.ordersExtendVo.isThirdOrder && item.source === 'ET_YLH')">
                <template v-if="item.displayPriceFlag">
                <div class="unit-price" v-if="item.poiName && item.orderType >=30 && item.orderType<=50">
                  <div>
                    {{itemChild.price}}{{item.poiName}}
                  </div>
                </div>
                <div class="unit-price" v-else>
                  <div>{{!!item.poiName ? item.poiName : '￥'}}</div>
                  <div>
                    {{itemChild.price | formatPrice}}
                  </div>
                </div>
                </template>
                </template>
                <div class="commodities">
                  <div>x</div>
                  <div>{{itemChild.number || '1'}}</div>
                </div>
              </div>
            </div>
            <div style="display: flex; flex-direction: row; justify-content: space-between; width: 4.7rem; margin-top: .2rem">
              <template v-if="item.orderType === 100 && itemChild.discounts && itemChild.discounts.length">
                <div v-for="(discount, disIndex) in itemChild.discounts" :key="disIndex" class="secondary">
                  <span>{{discount.cateName + discount.amount}}{{!!item.poiName ? item.poiName : '元'}}</span>
                </div>
              </template>
              <div class="secondary" v-else-if="Number(itemChild.orderPromotionAmount) > 0">
                <span>下单立减{{itemChild.orderPromotionAmount}}元</span>
              </div>
              <div v-else></div>
              <div style="flex: 0">
                <div class="commondity-comment" v-if="item.orderType !== 100 && itemChild.canComment" @click.stop="goComment(itemChild.cOrderSn)">立即评价</div>
                <div class="commondity-comment" v-else-if="item.orderType !== 100 && itemChild.canExperience" @click.stop="goExperience(itemChild.cOrderSn)">追加评价</div>
                <template v-if="itemChild.orderRepairs && (itemChild.stockType === 'STORE' || itemChild.stockType === 'O2O' || itemChild.stockType === 'SYN')">
                  <div class="repire-btn" @click.stop="toOrderRepair(itemChild.orderProductId)">退款详情</div>
                </template>
              </div>
            </div>
          </div>
        </div>
        <!-- <div v-if="itemChild.commentary" @click="toEvaluate(itemChild.cOrderSn)" class="evaluate-btn" :key="`toEvaluate${indexChild}`">
          评价
        </div> -->
      </template>
      <div class="total" v-if="item.orderStatusName !== '售后处理'">
        <template v-if="!(item.ordersExtendVo && item.ordersExtendVo.isThirdOrder && item.source === 'ET_YLH')">
        <div class="total-info">
          <div class="count">
            共{{item.productTotalNo || 0}}件商品
          </div>
          <div>
            合计:
          </div>
          <div class="total-price" v-if="item.poiName && item.orderType >=30 && item.orderType<=50">
            <div>
              {{item.totalAmount}} {{item.poiName}}
            </div>
          </div>
          <div class="total-price" v-else>
            <div>{{!!item.poiName ? item.poiName : '￥'}}</div>
            <div style="font-weight: bold;">
              {{item.totalAmount | fmtMoneyFilterInt}}<font>{{item.totalAmount | fmtMoneyFilterDecimal}}</font>
            </div>
            <br/>
          </div>
          <template v-if="((item.totalShippingFeeAmount + '').length + (item.totalAmount + '').length) <= 10">
            <div v-if="item.poiName && item.orderType >=30 && item.orderType<=50">
              (含运费{{item.totalShippingFeeAmount}}{{item.poiName}})
            </div>
            <div v-else>
              (含运费{{!!item.poiName ? item.poiName : '￥'}}{{item.totalShippingFeeAmount | formatPrice}})
            </div>
          </template>
          <template v-if="((item.totalShippingFeeAmount + '').length + (item.totalAmount + '').length) > 10">
            <div v-if="item.poiName && item.orderType >=30 && item.orderType<=50">
              (含运费{{item.totalShippingFeeAmount}}{{item.poiName}})
            </div>
            <div v-else>
              (含运费{{!!item.poiName ? item.poiName : '￥'}}{{item.totalShippingFeeAmount | formatPrice}})
            </div>
          </template>
        </div>
        </template>
        <div class="date">
          下单时间：{{item.addTime || ''}}
        </div>
      </div>
      <div class="number">
        订单号：{{item.orderSn || ''}}
      </div>
      <template v-if="item.orderType == 100">
        <div class="operation" v-if="item.ordersExtendVo && item.ordersExtendVo.isThirdOrder">
          <template v-if="item.ordersExtendVo && item.ordersExtendVo.operEvents && item.ordersExtendVo.operEvents.length && !(item.source === 'ET_STQY' && !isUplus)">
            <div v-for="(btn,index) in item.ordersExtendVo.operEvents "
              @click="goThridUrl(btn.linkUrl, item)"
              :key="index"
              class="returned-goods">
              {{btn.eventName}}
            </div>
          </template>
          <div v-if="item.immediatePay" @click="toPayment(item.orderSn)" class="orange">
            立即支付
          </div>
          <div
            class="tracking-orders"
            v-if="item.commentStatus"
            @click="goThridComment(item)"
          >
            {{item.commentStatus === 1 ? '去评价' : (item.commentStatus === 2 ? '追加评价' : '查看评价')}}
          </div>
          <div v-if="item.source === 'ET_YLH'" @click="trackingOrders(item)" class="tracking-orders">
            查看物流
          </div>
        </div>
      </template>
      <template v-else>
        <div class="operation-title" v-if="!item.isReceived">您的商品正在飞奔您的家中！</div>
        <div class="operation">
          <span class="group-buy" v-if="item.paidToShare" @click="shareToGroupBuy(item)">
            <span>邀请拼团</span>
          </span>
          <div v-if="item.canCancel" @click="cancellationOrder(item.orderId)" class="returned-goods">
            取消订单
          </div>
          <!-- <div v-if="item.canTrack" @click="trackingOrders(item.orderSn)" class="tracking-orders">
            追踪订单
          </div> -->
          <!-- <div v-if="item.isRightService" @click="toRepair" class="tracking-orders">
            售后/维修
          </div> -->
          <div v-if="item.canDelete" @click="deleteOrder(item.orderId)" class="returned-goods">
            删除订单
          </div>
          <div v-if="item.canCd" @click="dividOrder(item.orderId)" class="returned-goods">
            拆单发货
          </div>
          <div v-if="item.immediatePay" @click="toPayment(item.orderSn)" class="orange">
            立即支付
          </div>
          <div v-if="item.isMyOrder && item.payOnline && item.payRelationOrder" @click="toPaymentTail(item.relationOrderSn,item.paymentTimeOut)" :class="{'orange':true,'button--disabled':item.paymentTimeOut}">
            支付尾款
          </div>
          <!-- <div @click="repurchaseBuy(item[0].orderProductId)" v-if="item.orderStatusName === '已取消'" class="orange">
            再次购买
          </div> -->
          <div v-if="item.canTrack" @click="trackingOrders(item)" class="tracking-orders">
            查看物流
          </div>
          <div class="re-buy" v-if="item.buttonBuyAgain" @click="reBuy(item)">再次购买</div>
        </div>
      </template>
    </van-col>
  </van-row>
</template>

<script>
/*
 * 接口配置文件
*/
import personalApi from '@/http/service/index';
import { getUcLoginUrl } from '@/http/service/common.js'
// import ClipboardJS from 'clipboard'; // 复制插件
/**
 * vant框架
 */
import Vue from 'vue';
import { List, Toast, PullRefresh, Dialog } from 'vant';
// 全局注册
Vue.use({ PullRefresh, Toast, List, Dialog });

/*
 * 订单列表的list
*/
export default {
  name: 'list',
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return {}
      },
    },
    parentStatus: {
      type: null,
      default: function () {
        return {}
      },
    },
  },
  data() {
    return {
      isUplus: this.$util.isUplus()
    }
  },
  computed: {
    location() {
      return this.$store.getters['getlocationInfo']
    },
  },
  created() {
  },
  mounted() {

  },
  methods: {
    /** 邀请拼团 */
    shareToGroupBuy(order) {
      this.$router.push(`/groupBookingDetail?orderSn=${order.orderSn}`)
      // const shareUrl = `${location.origin}/sgmobile/groupBookingDetail?orderSn=${order.orderSn}`;
      // if (this.$util.isHaierApp()) {
      //   this.$util
      //     .inAppShare({
      //       title: '海尔智家',
      //       desc: '海尔智家好物拼团，邀你参与',
      //       url: shareUrl,
      //     })
      // } else {
      //   const clipboard = new ClipboardJS('.group-buy', {
      //     text: function(trigger) {
      //       return shareUrl
      //     }
      //   });

      //   clipboard.on('success', () => {
      //     this.$toast.success('链接复制成功');
      //   });

      //   clipboard.on('error', () => {
      //     this.$toast.success('复制失败');
      //   });
      // }
    },
    /**
     *追加评价
     */
    goExperience(cOrderSn) {
      this.$router.push({ path: '/review', query: { cOrderSn: cOrderSn } })
    },
    /**
     * @description 再次购买 批量添加购物车

    */
    reBuy(i) {
      let param = {
        productParams: [],
        streetId: this.location.townshipId,
        fromPage: document.URL,
      }
      i.orderProducts.forEach(o => {
        let product = {
          productId: o.productId,
          number: o.number,
          // spu: o.attrName ? '' : o.sku,
          // sku: o.attrName ? o.sku : '',
          attrValueNames: o.attrName,
        }
        if (o.attrName) {
          product.sku = o.sku
        } else {
          product.spu = o.sku
        }
        param.productParams.push(product)
      })

      personalApi.addBatch(param).then(res => {
        if (res.success) {
          this.$router.push({
            path: `/cart`,
          });
        } else {
          Toast.fail(res.message);
        }
      })
      console.log('reBuy:', param)
    },
    /**
     * @description 跳转到查看详情页面
     * @param {}
    */
    viewDetail (itemChild, item) {
      if (item.source === 'ET_YLH') {
        // 不做处理
      } else if (item.ordersExtendVo && item.ordersExtendVo.isThirdOrder) {
        if (itemChild.linkUrl) {
          this.goThridUrl(itemChild.linkUrl, item)
        }
      } else {
        const context = this;
        context.$router.push({
          path: `/orderDetail`,
          query: {
            cOrderSn: itemChild.cOrderSn,
            cOrderId: itemChild.orderProductId,
          }
        });
      }
    },

    /**
     * @description  追踪订单
     * @param {canClickConfirm} true可以点击按钮 false不能点击按钮
    */
    trackingOrders (item) {
      if (item.source === 'ET_YLH') {
        //  中间页
        this.$router.push({
          path: `/trackingDetail?soNo=${item.orderSn}`
        });
        return
      }
      const context = this;
      this.$util.gioTrackWithVariable('mall_order_viewlogistics', {
        order_id: item.orderSn || 'null',
      }).then(() => {
        this.initTrackinfo(item.orderSn).then(res => {
          switch (res) {
            case 1:
            // 1 跳转物流追踪页
            // 区分跳转地图页还是物流跟踪页逻辑  b2b2c并且 自营或者oco的 跳地图页，其他情况以及待确定/已取消状态的订单跳旧物流跟踪页
              if (
                item.orderStatus !== 202 &&
                item.orderStatus !== 200 &&
                item.orderProducts[0].shippingMode === 'B2B2C' &&
                (item.orderProducts[0].stockType === 'WA' || item.orderProducts[0].stockType === 'OCO')) {
                context.$router.push({
                  path: `/mapTrackingOrders?cOrderSn=${item.orderProducts[0].cOrderSn}`
                });
              } else {
                context.$router.push({
                  path: `/trackingOrders?cOrderSn=${item.orderProducts[0].cOrderSn}`
                });
              }
              break;
            case 2:
              // 2 中间页
              context.$router.push({
                path: `/trackingDetail?orderSn=${item.orderSn}`
              });
              break;
          }
        })
      })
    },

    /**
     * @description 删除订单
     * @param
    */
    deleteOrder (orderId) {
      const content = this;
      this.$dialog.confirm({
        allowHtml: true,
        title: '您确定要删除当前订单吗？',
        message: `<span style="color: #666">订单删除后将无法恢复，可能影响订单的退货/退款</span>`,
        cancelButtonColor: '#666',
        confirmButtonText: '确定'
        // className: 'dialog-custom',
      }).then(() => {
        // on confirm
        const data = {
          orderId: orderId
        };
        personalApi.deleteOrder(data).then(res => {
          const _resData = res.data;
          if (_resData.success) {
            Toast.success('删除成功');
            /**
             * @description 下拉刷新,下啦刷新同时清空现有数据，页码初始化0
             */
            content.$emit('onRefreshParent');
          } else {
            Toast.fail(_resData.message);
          }
        })
      }).catch(() => {
        // on cancel
      });
    },
    /**
     * @description 拆单发货
     */
    dividOrder(orderId) {
      Toast('请到智家掌柜APP支付尾款')
    },
    /**
     * 跳转第三方订单页面
     */
    goThridUrl(url, order) {
      if (order.source === 'ET_STQY' && !this.isUplus) {
        // 生态权益订单由于连接问题，只在智家APP可跳转
        return
      }
      if (url) {
        getUcLoginUrl({
          redirectUri: url,
          target: order.ordersExtendVo.authTarget || '',
          targetTest: order.ordersExtendVo.authTarget || '',
          redirectUriTest: url,
        }).then(({ data }) => {
          if (data.success) {
            this.$util.debounceImmediate(this.$util.openNewWindow(data.data, true, false, false))
            // this.$util.openNewWindow(data.data, true, false, false);
          } else {
            this.$util.debounceImmediate(this.$util.openNewWindow(url, true, false, false))
            // this.$util.openNewWindow(url, true, false, false);
          }
        })
      }
    },
    /**
     * @description 取消订单
     * @param
    */
    cancellationOrder (orderId) {
      const content = this;
      this.$dialog.confirm({
        title: '提示',
        message: '您确定要取消当前订单吗？',
        // className: 'dialog-custom',
      }).then(() => {
        // on confirm
        const data = {
          orderId: orderId,
          cancelFlag: 'mstore',
        };
        personalApi.cancelOrder(data).then(res => {
          const _resData = res.data;
          if (_resData.success) {
            Toast.success('取消成功');
            /**
             * @description 下拉刷新,下啦刷新同时清空现有数据，页码初始化0
             */
            content.$emit('onRefreshParent');
          } else {
            Toast.fail(_resData.message);
          }
        })
      }).catch(() => {
        // on cancel
      });
    },

    /**
     * 去售后维修
     */
    toRepair() {
      this.$router.push('/repair')
    },

    /**
     * 去评价
     */
    goComment(cOrderSn) {
      this.$router.push(`/evaluate?cOrderSn=${cOrderSn}`)
    },

    /**
     * 外部订单评价
     * commentStatus 0-不可评价  1- 去评价 2-追评 3-查看评价
     */
    goThridComment(order) {
      this.$router.push(`/thridEvaluate?orderSn=${order.orderSn}&type=${order.commentStatus || 0}`)
    },

    /**
     * @description 立即支付
     * @param
    */
    toPayment (orderSn) {
      const context = this;
      context.$router.push({
        path: `/orderCashier/${orderSn}`,
      });
    },

    /**
     * @description 支付尾款  尾款支付时要调用接口校验 判断是1跳转支付页面 还是 0尾款结算页面
     * @param
    */
    toPaymentTail (orderSn, paymentTimeOut) {
      if (paymentTimeOut) {
        return
      }
      const context = this;
      const data = {
        orderSn: orderSn,
      };
      // Toast.loading({
      //   message: '加载中...',
      //   forbidClick: true,
      //   loadingType: 'spinner',
      //   duration: 0
      // });
      personalApi.checkTailOrder(data).then(res => {
        const _resData = res;
        // 手动清除 Toast
        Toast.clear();
        if (_resData.success) {
          if (_resData.data === 0) {
            /**
             * @description 支付尾款 创建支付尾款的结算订单
            */
            context.createTailOrder(orderSn);
          } else {
            context.$router.push({
              path: `/orderCashier/${orderSn}`
            });
          }
        } else {
          Toast.fail(_resData.message);
        }
      })
    },

    /**
     * @description 支付尾款 创建支付尾款的结算订单，订单创建成功后 跳转订单结算页面
     * @param
    */
    createTailOrder (orderSn) {
      const context = this;
      const data = {
        orderSn: orderSn,
      };
      personalApi.createTailOrder(data).then(res => {
        const _resData = res.data;
        // 手动清除 Toast
        Toast.clear();
        if (_resData.success) {
          context.$router.push({
            path: `/orderTail?orderSn=${orderSn}`
          });
        } else {
          Toast.fail(_resData.message);
        }
      })
    },
    /**
     * @description 获取物流追踪信息
    */
    initTrackinfo(orderSn) {
      return new Promise((resolve, reject) => {
        const param = {
          orderSn: orderSn, // 订单号 必传
        }
        personalApi.getTrackFlowOrderDetail(param).then(res => {
          if (res.data.success) {
            resolve(res.data.data.pageUrlStatus)
          }
        })
      })
    },
    /**
     * 跳转退款详情
    */
    toOrderRepair(orderProductId) {
      this.$router.push({ path: '/details', query: { orderProductId: orderProductId } }) // 退货详情回传网单id
    }
  },
}
</script>

<style lang="scss" scoped>

