    padding: .06rem .12rem;
    box-sizing: border-box;
    span {
      line-height: .32rem;
      font-size: .24rem;
      color: #2878FF;
      font-weight: bold;
      display: inline-block;
      transform: scale(.91);
    }
  }
  .state {
    font-size: 0.28rem;
    font-weight: 400;
    color: $themeColor;
    // line-height: 0.80rem;
  }

  .color-wine-red {
    color: $themeColor;
  }

  .commodity {
    display: flex;
    justify-content: space-between;
    padding: 0 0.24rem 0.26rem 0.24rem;
    position: relative;
    width: 7.02rem;
    box-sizing: border-box;
    /* overflow: hidden; */
    // border-bottom: 0.01rem solid rgba(238,238,238,1);
  }

  .thumbnail {
    width: 1.60rem;
    min-width: 1.6rem;
    height: 1.60rem;
    margin: 0 0.24rem 0 0;
