    margin-right: .1rem;
  }

  .reserve {
    position: absolute;
    background: $themeColor;
    width: 1.6rem;
    font-size: 0.28rem;
    color: #ffffff;
    text-align: center;
    height: 0.4rem;
    line-height: 0.4rem;
  }

  .product-title {
    font-size: 0.28rem;
    font-weight: 400;
    color: rgba(51,51,51,1);
    line-height: 0.40rem;
    margin: 0 0 0.04rem 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    /* width: 2.88rem; */
    word-break: break-all;
  }

  .model {
    margin: 0 0 0.08rem 0;
    font-size: 0.24rem;
